import { useState } from 'react'
import { Slash } from 'react-feather'
import { ImageProps } from 'rebass'

import useTheme from '../../hooks/useTheme'

const BAD_SRCS: { [tokenAddress: string]: true } = {}

interface LogoProps extends Pick<ImageProps, 'style' | 'alt' | 'className'> {
  srcs: string[]
}

/**
 * Renders an image by sequentially trying a list of URIs, and then eventually a fallback triangle alert
 */
export default function Logo({ srcs, alt, style, ...rest }: LogoProps) {
  const [, refresh] = useState<number>(0)

  const theme = useTheme()

  let src: string | undefined = srcs.find((src) => !BAD_SRCS[src])

  if (src) {
    if (alt === 'ATN logo') {
      src = 'http://biobridge-app.oss-cn-shenzhen.aliyuncs.com/Image/TokenLogo/atn-logo.png'
    }
    if (alt === 'MBT logo') {
      src = 'http://biobridge-app.oss-cn-shenzhen.aliyuncs.com/Image/TokenLogo/mbt-logo.png'
    }
    if (alt === 'MECoin logo') {
      src = 'http://biobridge-app.oss-cn-shenzhen.aliyuncs.com/Image/TokenLogo/mecoin-logo.png'
    }
    if (alt === 'POHC logo') {
      src = 'http://biobridge-app.oss-cn-shenzhen.aliyuncs.com/Image/TokenLogo/pohc-logo.png'
    }
    return (
      <img
        {...rest}
        alt={alt}
        src={src}
        style={style}
        onError={() => {
          if (src) BAD_SRCS[src] = true
          refresh((i) => i + 1)
        }}
      />
    )
  }

  return <Slash {...rest} style={{ ...style, color: theme.bg4 }} />
}
